@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600;700&display=swap);
*{box-sizing:border-box}body{margin:0;font-family:'Barlow Semi Condensed', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;display:flex;justify-content:center;background-image:radial-gradient(farthest-corner at 50% 15%, #1f3756 15%, #141539 100%)}button{font-family:inherit;cursor:pointer}

.app{display:flex;flex-direction:column;align-items:center;width:100vw;max-width:900px;min-height:100vh;padding:2rem;position:relative}

.game .choice-wrapper{margin-top:3.5rem;transform:scale(0.75)}.game .choice-wrapper .bg-img--pentagon{position:relative;z-index:-10}.game .choice-wrapper .choice{position:absolute}.game .choice-wrapper .choice--lizard{left:0;bottom:-70px}.game .choice-wrapper .choice--paper{right:-70px;top:50px}.game .choice-wrapper .choice--rock{right:0;bottom:-70px}.game .choice-wrapper .choice--scissors{top:-70px;left:100px}.game .choice-wrapper .choice--spock{left:-70px;top:50px}@media screen and (min-width: 1024px){.game .choice-wrapper{transform:scale(1);margin-top:5rem}}

.choice{position:relative;background:#fff;background-clip:padding-box;border-radius:50%}.choice:before{content:'';position:absolute;z-index:-1;top:0;bottom:0;left:0;right:0;border-radius:inherit}.choice--button,.choice--div{display:flex;align-items:center;justify-content:center;width:140px;height:140px;border:solid 17px transparent;box-shadow:inset 0 7px 1px -1px rgba(185,191,213,0.5)}.choice--button.choice--lizard:before,.choice--div.choice--lizard:before{background:linear-gradient(180deg, #834fe3 0%, #8c5de5 100%);box-shadow:inset 0 -7px 2px -1px #6d3dc7;margin:-17px}.choice--button.choice--paper:before,.choice--div.choice--paper:before{background:linear-gradient(180deg, #4865f4 0%, #5671f5 100%);box-shadow:inset 0 -7px 2px -1px #2f49ca;margin:-17px}.choice--button.choice--rock:before,.choice--div.choice--rock:before{background:linear-gradient(180deg, #dc2e4e 0%, #dd405d 100%);box-shadow:inset 0 -7px 2px -1px #a31933;margin:-17px}.choice--button.choice--scissors:before,.choice--div.choice--scissors:before{background:linear-gradient(180deg, #ec9e0e 0%, #eca922 100%);box-shadow:inset 0 -7px 2px -1px #c97003;margin:-17px}.choice--button.choice--spock:before,.choice--div.choice--spock:before{background:linear-gradient(180deg, #40b9ce 0%, #52bed1 100%);box-shadow:inset 0 -7px 2px -1px #268697;margin:-17px}@media screen and (min-width: 1024px){.choice--div{width:280px;height:280px;border:solid 34px transparent;box-shadow:inset 0 12px 1px -1px rgba(185,191,213,0.5)}.choice--div.choice--lizard:before{background:linear-gradient(180deg, #834fe3 0%, #8c5de5 100%);box-shadow:inset 0 -12px 2px -1px #6d3dc7;margin:-34px}.choice--div.choice--paper:before{background:linear-gradient(180deg, #4865f4 0%, #5671f5 100%);box-shadow:inset 0 -12px 2px -1px #2f49ca;margin:-34px}.choice--div.choice--rock:before{background:linear-gradient(180deg, #dc2e4e 0%, #dd405d 100%);box-shadow:inset 0 -12px 2px -1px #a31933;margin:-34px}.choice--div.choice--scissors:before{background:linear-gradient(180deg, #ec9e0e 0%, #eca922 100%);box-shadow:inset 0 -12px 2px -1px #c97003;margin:-34px}.choice--div.choice--spock:before{background:linear-gradient(180deg, #40b9ce 0%, #52bed1 100%);box-shadow:inset 0 -12px 2px -1px #268697;margin:-34px}.choice--div .icon{width:5.5rem}}

@-webkit-keyframes bounce{0%{transform:scale(0)}40%{transform:scale(1.3)}60%{transform:scale(0.8)}80%{transform:scale(1.1)}100%{transform:scale(1)}}@keyframes bounce{0%{transform:scale(0)}40%{transform:scale(1.3)}60%{transform:scale(0.8)}80%{transform:scale(1.1)}100%{transform:scale(1)}}@-webkit-keyframes unfoldIn{0%{transform:scaleY(0.005) scaleX(0)}50%{transform:scaleY(0.005) scaleX(1)}100%{transform:scaleY(1) scaleX(1)}}@keyframes unfoldIn{0%{transform:scaleY(0.005) scaleX(0)}50%{transform:scaleY(0.005) scaleX(1)}100%{transform:scaleY(1) scaleX(1)}}@-webkit-keyframes zoomIn{0%{transform:scale(0)}100%{transform:scale(1)}}@keyframes zoomIn{0%{transform:scale(0)}100%{transform:scale(1)}}@-webkit-keyframes popIn{0%{transform:scale(0)}80%{transform:scale(1.2)}100%{transform:scale(1)}}@keyframes popIn{0%{transform:scale(0)}80%{transform:scale(1.2)}100%{transform:scale(1)}}@-webkit-keyframes jump{0%{transform:scale(1)}70%{transform:scale(1.3)}100%{transform:scale(1)}}@keyframes jump{0%{transform:scale(1)}70%{transform:scale(1.3)}100%{transform:scale(1)}}.result{display:flex;align-items:center;margin-top:2rem}.result .pick-wrapper{display:flex;flex-direction:column;align-items:center;margin:1rem 1.5rem 1rem 0;transition:all 1s}.result .pick-wrapper:nth-of-type(2){margin-right:0;margin-left:1.5rem}.result .pick-wrapper h3{color:#fff;font-size:0.8rem;letter-spacing:0.15rem;margin-top:1.5rem;order:1;text-transform:uppercase}.result .pick-wrapper .pick--empty{width:calc(140px - 17px * 2);height:calc(140px - 17px * 2);margin:17px;border-radius:50%;background-color:rgba(0,0,0,0.15);-webkit-animation:jump .8s ease-in 0.2s forwards;animation:jump .8s ease-in 0.2s forwards}.result__box-wrapper{position:absolute;left:50%;bottom:20%}.result__box-wrapper .result__box{display:flex;flex-direction:column;align-items:center;margin-top:3rem;order:2;position:relative;left:-50%}.result__box-wrapper .result__box .result__title{color:#fff;font-size:3rem;margin-bottom:1rem;text-transform:uppercase;transform:scale(0);-webkit-animation:popIn 0.3s 0.8s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;animation:popIn 0.3s 0.8s cubic-bezier(0.165, 0.84, 0.44, 1) forwards}.result__box-wrapper .result__box .play-again{color:#575c75;background-color:#fff;text-transform:uppercase;padding:0.75rem 3rem;border-radius:8px;letter-spacing:0.1rem;border:none;box-shadow:0px 3px 5px 2px rgba(0,0,0,0.25);transform:scale(0);-webkit-animation:popIn 0.3s 1.2s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;animation:popIn 0.3s 1.2s cubic-bezier(0.165, 0.84, 0.44, 1) forwards}@media screen and (min-width: 1024px){.result{margin-top:0}.result--final .pick-wrapper{transform:translateX(-93px)}.result--final .pick-wrapper--house{transform:translateX(93px)}.result .pick-wrapper{margin:1rem 2.5rem 1rem 0}.result .pick-wrapper:nth-of-type(2){margin-left:2.5rem}.result .pick-wrapper h3{font-size:1.4rem;margin-top:inherit;margin-bottom:3rem;order:0}.result .pick-wrapper .pick--empty{width:calc(280px - 34px * 2);height:calc(280px - 34px * 2);margin:34px}.result__box-wrapper{bottom:unset}}

@-webkit-keyframes bounce{0%{transform:scale(0)}40%{transform:scale(1.3)}60%{transform:scale(0.8)}80%{transform:scale(1.1)}100%{transform:scale(1)}}@keyframes bounce{0%{transform:scale(0)}40%{transform:scale(1.3)}60%{transform:scale(0.8)}80%{transform:scale(1.1)}100%{transform:scale(1)}}@-webkit-keyframes unfoldIn{0%{transform:scaleY(0.005) scaleX(0)}50%{transform:scaleY(0.005) scaleX(1)}100%{transform:scaleY(1) scaleX(1)}}@keyframes unfoldIn{0%{transform:scaleY(0.005) scaleX(0)}50%{transform:scaleY(0.005) scaleX(1)}100%{transform:scaleY(1) scaleX(1)}}@-webkit-keyframes zoomIn{0%{transform:scale(0)}100%{transform:scale(1)}}@keyframes zoomIn{0%{transform:scale(0)}100%{transform:scale(1)}}@-webkit-keyframes popIn{0%{transform:scale(0)}80%{transform:scale(1.2)}100%{transform:scale(1)}}@keyframes popIn{0%{transform:scale(0)}80%{transform:scale(1.2)}100%{transform:scale(1)}}@-webkit-keyframes jump{0%{transform:scale(1)}70%{transform:scale(1.3)}100%{transform:scale(1)}}@keyframes jump{0%{transform:scale(1)}70%{transform:scale(1.3)}100%{transform:scale(1)}}.rules-btn{background-color:transparent;border:2px solid #7e8ba0;border-radius:8px;color:#d6dce6;font-size:1rem;font-weight:700;padding:0.60rem 1.9rem;letter-spacing:2px;line-height:1;text-transform:uppercase;margin:0 auto 3rem auto;position:absolute;bottom:0;left:0;right:0}@media screen and (min-width: 1024px){.rules-btn{position:absolute;margin-bottom:2rem;margin-right:2rem;right:0;bottom:0}}#rules-modal-container .rules-modal-wrapper{width:100vw;height:100vh;position:fixed;z-index:10;top:0;left:0;transform:scale(0)}#rules-modal-container .rules-modal-wrapper .rules-modal{display:flex;flex-direction:column;align-items:center;background-color:#fff;padding:2rem;height:100%}#rules-modal-container .rules-modal-wrapper .rules-modal__header{display:flex;align-items:center;flex-direction:column;margin-top:3rem}#rules-modal-container .rules-modal-wrapper .rules-modal__title{margin:0;text-transform:uppercase;font-size:2rem;color:#3b4363;line-height:1}#rules-modal-container .rules-modal-wrapper .rules-modal__close{display:flex;align-items:center;justify-content:center;background-color:transparent;border:none;padding:0.25rem;position:absolute;bottom:0;margin-bottom:3rem}#rules-modal-container .rules-modal-wrapper .rules-modal__content{margin:6rem 0 1rem 0}#rules-modal-container.rules-modal--active .rules-modal-wrapper{-webkit-animation:unfoldIn 0.6s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;animation:unfoldIn 0.6s cubic-bezier(0.165, 0.84, 0.44, 1) forwards}@media screen and (min-width: 1024px){#rules-modal-container .rules-modal-wrapper{display:flex;justify-content:center;align-items:center;background-color:rgba(0,0,0,0.25)}#rules-modal-container .rules-modal-wrapper .rules-modal{border-radius:0.5rem;height:auto}#rules-modal-container .rules-modal-wrapper .rules-modal__header{justify-content:space-between;flex-direction:row;width:100%;margin:0}#rules-modal-container .rules-modal-wrapper .rules-modal__close{margin-right:-0.25rem;position:unset;margin-bottom:0}#rules-modal-container .rules-modal-wrapper .rules-modal__content{margin:1rem 0}#rules-modal-container.rules-modal--active .rules-modal-wrapper{transform:scaleY(0.01) scaleX(0)}#rules-modal-container.rules-modal--active .rules-modal-wrapper .rules-modal{transform:scale(0);-webkit-animation:zoomIn 0.5s 0.6s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;animation:zoomIn 0.5s 0.6s cubic-bezier(0.165, 0.84, 0.44, 1) forwards}}

.header{align-items:center;border:3px solid #606e85;border-radius:7px;display:flex;justify-content:space-between;margin-bottom:2rem;padding:.75rem;width:100%;max-width:650px}.header .logo{padding:.25rem .75rem;height:70px}.score-container{background-color:white;border-radius:5px;padding:0.75rem 1.7rem;text-align:center}.score-container h3{color:#2a46c0;font-size:0.75rem;font-weight:600;letter-spacing:2px;margin:0;text-transform:uppercase}.score-container .score{font-size:3rem;line-height:1;color:#575c75;font-weight:700}@media screen and (min-width: 1024px){.header{border-radius:20px;padding:1rem 1.25rem}.header .logo{padding:0;height:100%}.score-container{border-radius:10px;padding:1rem 3rem}.score-container h3{font-size:1rem}.score-container .score{font-size:4rem;line-height:1}}

